.clickable{
    cursor:pointer;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
    supported by Chrome, Edge, Opera and Firefox */
}

.hover {
    transition: box-shadow 0.2s, transform 0.3s;
    border-radius:8px;
    padding:15px;
    transform: scale(0.99);
}
.hover:hover {
    transform: scale(1);
    box-shadow: inset 0 0 0 500px rgba(255,255,255,0.3);
}

body {
    background:#313638;
    margin:0;
}

.navbar {
    height:80px;
    display:flex;
    flex-direction:row;
    justify-content:space-evenly;
    align-items: center;

    color:#FFFFFF;
    font-family: 'Josefin Sans', sans-serif;
    border-bottom: 1px solid rgba(255, 255, 255, 0.25);

    background: rgba(0,0,0,0.2);

    padding:0 15px;
}

.navbar > * {
    margin:0 15px;
}
.navbar-logo {
    display:flex;
    align-items: center;
    flex-direction:row;
    justify-content:space-evenly;
    text-decoration:none;
    color:#ffffff;
}

.navbar-logo img{
    width:30px;
    height:30px;
}

.navbar-logo div{
    padding:0px 15px;
    font-size:35px;
    font-weight:600;
}

.navbar-search{
    flex-grow:2;
    max-width:60%;
    height:45px;
    display:flex;
    flex-direction:row;
    justify-content:space-between;
    align-items: center;
    background: rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.1);
    box-sizing: border-box;
    border-radius: 7px;
}

.navbar-search input{
    height:100%;
    flex-grow:2;
    color:#ffffff;
    background:none;
    border:none;

    font-size:20px;

    font-family: 'Roboto', sans-serif;
}
.navbar-search input::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgba(255,255,255,0.2);
    opacity: 1; /* Firefox */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.navbar-search input:-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgba(255,255,255,0.2);
    opacity: 1; /* Firefox */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.navbar-search input::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgba(255,255,255,0.2);
    opacity: 1; /* Firefox */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.navbar-search img{
    padding:15px;
    width:25px;
    height:25px;
}

.navbar-upload {
    width:30px;
    height:30px;
}

.navbar-upload img {
    width:30px;
    height:30px;
}
.navbar-profile {
    display:flex;
    flex-direction:row;
    justify-content:space-evenly;
    align-items: center;
}

.navbar-profile img {
    width:50px;
    height:50px;
    border-radius: 100%;
    margin:5px;
    margin-right:16px;
}
.navbar-profile-content{
    font-size:12px;
    font-weight:600;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}


.navbar-profile-name {
    text-decoration:none;
    color:white;
    font-size:20px;
}

.navbar-profile-pens {
    color:#AAAAAA;
}

.navbar-signin {
    font-size:20px;
    font-weight:800;
    line-height:1;
    font-family: 'Roboto', sans-serif;
    text-transform:uppercase ;
}

.navbar-profile-dropdown{
    font-family: 'Roboto', sans-serif;
    position:absolute;
    right:0;
    background: #313638;
    font-size:24px;
    font-weight:1000;
    display:flex;
    flex-direction:column;
    border-radius: 8px;
}


.navbar-profile-dropdown > * {
    background: rgba(0,0,0,.2);
    padding:32px;
    color:#ffffff;
    text-decoration:none;
    text-align: center;
}


.modal-darken {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    z-index:5;
}

.modal-main {
    position:fixed;
    background: white;
    width: 80vw;
    max-width: 80vh;
    height: 60vw;
    max-height: 60vh;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    border-radius:15px;
    display:flex;
    flex-direction:column;
    z-index:5;
}

.uploadModal{
    height: 85vw;
    max-height: 85vh;
}

.modal-close{
    width:15px;
    height:15px;
    position:absolute;
    top:0px;
    right:0px;
}

.display-block {
    display: block;
}

.display-none {
    display: none;
}

.tabs{
    display:flex;
    flex-direction:row;
    align-content:center;
    justify-content:center;
}

.tabs > * {
    text-transform:uppercase ;
    background:none;
    border:none;
    font-family: 'Roboto', sans-serif;
    font-size:20px;
    font-weight:800;
    padding:15px;
    background-color:rgba(0,0,0,0);
    border-bottom: 4px solid #999999;
}

.uploaded-files{
