    padding:15px;
    margin:8px 45px;
    position:relative;
}
.file-upload-header{
    display:flex;
    flex-direction:row;
}

.file-upload-header > *{
    margin-right:4px;
}

.file-upload-inputs{
    display:flex;
    flex-direction:column;
}

.file-upload-inputs textarea,.file-upload-inputs input,.file-upload-inputs select{
    margin:4px;
    padding:4px;
    border:none;
    background-color:rgba(0,0,0,0.1);
