}

.file-upload-inputs-input{
    background:#E5E5E5;
}

.active {
    border-bottom: 4px solid #000000;
    background-color:rgba(0,0,0,0.2);
}

.accounts{
    font-family: 'Roboto', sans-serif;
    display:flex;
    flex-grow:2;
    flex-direction:column;
    justify-content:center;
}

.input{
    margin:0px auto; display:block;
    width:50vw;
    max-width:50vh;
    margin-bottom:16px;
    display:flex;
    flex-direction:column;
}

.input div{
    font-size:24px;
}


.input input{
    font-size:20px;
    background: #eeeeee;
    border:none;
    border-bottom: 2px solid #000000;
    border-radius:7px 7px 0px 0px;
    padding:4px;
}

.input-error {
    background: #FFeeee !important;
    border-bottom: 2px solid #FF0000 !important;
}

.input-success {
    background: #eeFFee !important;
    border-bottom: 2px solid #00FF00 !important;
}
.error-message{
    color:#FF0000;
    font-size:16px !important;
}

.accounts button{
    margin:0px auto; 
    display:block;
    font-size:20px;
    color: #00A3FF;
    background:#CCECFF;
    border:none;
    font-weight:600;
}

.uploadArea {
    width:90%;
    height:40%;
    margin:5%;
    background: rgba(0, 163, 255, 0.1);
    border-radius:15px;
    border: 2px dashed #00A3FF;
