@media screen and (max-width: 900px) {
    .navbar {
        flex-wrap: wrap;
        height:120px;
        padding:0 5px;
    }

    .navbar > * {
        margin:0 0px;
    }
    .navbar-logo{
        order:2;
    }
    .navbar-logo > img {
        display:none;
    }
    .navbar-upload{
        order:1;
    }
    .navbar-profile{
        order:3;
    }
    .navbar-profile-content {
        display:none;
    }
    .navbar-search{
        width:95%;
        max-width:95%;
        order:4;
        margin-bottom:15px;
    }
    .modal-main {
        height: 120vw;
    }
}

